.StatusComp .status-btn {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  position: absolute;
  top: 30px;
  margin-top: -30px;
  right: 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 30px;
  color: #fff;
  -webkit-transition: -webkit-transform 0.5s cubic-bezier(.09,.73,.37,.99);
  transition: -webkit-transform 0.5s cubic-bezier(.09,.73,.37,.99);
  -o-transition: transform 0.5s cubic-bezier(.09,.73,.37,.99);
  transition: transform 0.5s cubic-bezier(.09,.73,.37,.99);
  transition: transform 0.5s cubic-bezier(.09,.73,.37,.99), -webkit-transform 0.5s cubic-bezier(.09,.73,.37,.99);
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}



